import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {AuthGuardService} from "../../services/security/auth-guard.service";
import {ExpandablerowComponent} from "./expandablerow.component";

const serverRoutes: Routes = [
  { path: '',  component: ExpandablerowComponent,  canActivate: [AuthGuardService] }

];

@NgModule({
  imports: [RouterModule.forChild(serverRoutes)],
  exports: [RouterModule]
})
export class ExpandablerowRoutingModule { }
